oped(SCOPES);\n            NetHttpTransport httpTransport = GoogleNetHttpTransport.newTrustedTransport();\n            return new Sheets.Builder(httpTransport, JSON_FACTORY, credential).setApplicationName(APPLICATION_NAME).build();\n        } catch (Exception e) {\n            e.printStackTrace();\n        }\n        return null;\n    }\n\n}\n          ")])]},proxy:true}])})],1),_c('div',{staticClass:"col-md-8 col-12"},[_c('CodeContainer',{attrs:{"title":'GsaService'},scopedSlots:_vm._u([{key:"sourceCode",fn:function(){return [_c('pre',[_vm._v("import com.google.api.services.sheets.v4.Sheets;\nimport com.google.api.services.sheets.v4.model.ValueRange;\nimport org.springframework.stereotype.Service;\n\nimport javax.annotation.Resource;\nimport java.util.Collections;\nimport java.util.List;\n\n@Service\npublic class GsaService {\n\n    @Resource\n    SheetsService sheetsService;\n\n    public List<List<Object>> readData(String sheetId, String range) {\n        try {\n            Sheets sheets = sheetsService.getSheets();\n            ValueRange response = sheets.spreadsheets().values().get(sheetId, range).execute();\n            return response.getValues();\n        } catch (Exception e) {\n            e.printStackTrace();\n        }\n        return null;\n    }\n\n    public void insertData(String sheetId, String range, List<Object> newData) {\n        try {\n            Sheets sheets = sheetsService.getSheets();\n            ValueRange appendBody = new ValueRange().setValues(Collections.singletonList(newData));\n            sheets.spreadsheets()\n                    .values()\n                    .append(sheetId, range, appendBody)\n                    .setValueInputOption(\"USER_ENTERED\")\n                    .setInsertDataOption(\"INSERT_ROWS\")\n                    .setIncludeValuesInResponse(true)\n                    .execute();\n        } catch (Exception e) {\n            e.printStackTrace();\n        }\n    }\n\n    public void updateData(String sheetId, String range, List<Object> newData) {\n        try {\n            Sheets sheets = sheetsService.getSheets();\n            ValueRange updateBody = new ValueRange().setValues(Collections.singletonList(newData));\n            sheets.spreadsheets()\n                    .values()\n                    .update(sheetId, range, updateBody)\n                    .setValueInputOption(\"RAW\")\n                    .execute();\n        } catch (Exception e) {\n            e.printStackTrace();\n        }\n    }\n\n    // String sheetId = \"1L6Wduy1cv3ze_ASGAHWHDi14uUc85g_lMASKMtmt1Js\";\n    // String range = \"Sheet01!A1:C1\";\n    // List<Object> newData = Arrays.asList(3, \"Kevin\", 100);\n\n}\n          ")])]},proxy:true}])})],1)])])}
